import React from 'react';
import { Container } from 'react-bootstrap';
import { PageHeaderImage } from '../components';
import { Button, Card, Row, Col } from 'react-bootstrap';

class Contact extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      pageTitle: 'Contact',
    };
  }

  render() {
    const { pageTitle } = this.state;

    return (
      <React.Fragment>
        <PageHeaderImage pageTitle={pageTitle} backgroundImageSource={`${process.env.PUBLIC_URL}/img/page_header/mori.png`} />
        <Container>
          <div className="mt-5">
            <p></p>
            <p></p>
          </div>

          <div className="mt-5">
            <Row>
              <Col>
                <h2>Twitter</h2>
                <Card>
                  <Row>
                    <Col>
                      <Card.Img variant="top" src={`${process.env.PUBLIC_URL}/img/uzia_icon_sample.png`} />
                    </Col>
                    <Col>
                      <Card.Body>
                        <Card.Text>TwitterDM</Card.Text>
                        <Button variant="primary" href="/">Twitter DM</Button>
                      </Card.Body>
                    </Col>
                  </Row>
                </Card>
              </Col>
              <Col>
                <h2></h2>
                <Card>
                  <Card.Text></Card.Text>
                  <Col>
                    <h1></h1>
                  </Col>
                </Card>
              </Col>
            </Row>
          </div>
        </Container>
      </React.Fragment>
    );
  }
}

export default Contact;